import { getCodeName, removeEl, isObjEmpty } from './utils';
import { InstanceState, State } from './State';
import { spend, completeResearchUpgrade } from '.';
import { UpdatesState } from './Updates';
import { generateRisingText } from './RisingText';
import { hideTooltip } from './Tooltip';
import { Sounds } from './Sounds';

// once quest board is unlocked,
// upgrade called Boots of Swiftness
// that'll increase quest speed
const SECOND = 1000;
const MINUTE = 1000 * 60;
const HOUR = MINUTE * 60;

export interface ResearchUpgrade {
    name: string;
    codeName?: string;
    desc: string;
    flavorText?: string;
    powerNeeded: number;
    cost: number;
    requires?: {};
    isLocked: boolean;
    isInProgress?: boolean;
    isNew?: boolean;
    isOwned?: boolean;
    isRepeatable?: boolean;
    priceIncrease?: number;
    complete?: () => void;
    start?: () => void;
    buy?: (event: MouseEvent) => void;
    removeNew?: (event: MouseEvent) => void;
    startedOn?: Date;
    completedOn?: Date;
    onComplete?: {};
}

const ResearchUpgrade = function (u) {
    this.name = u.name;
    this.codeName = getCodeName(u.name);
    this.desc = u.desc;
    this.flavorText = u.flavorText || null;
    this.powerNeeded = u.powerNeeded;
    this.cost = u.cost;
    this.requires = u.requires || [];
    this.isLocked = u.isLocked;
    this.isInProgress = u.isInProgress || false;
    this.isNew = u.isNew || true;
    this.isOwned = u.isOwned || false;
    this.isRepeatable = u.isRepeatable || false;
    if (this.isRepeatable) this.priceIncrease = u.priceIncrease;
    this.onComplete = u.onComplete || null;

    this.complete = () => {
        this.completedOn = new Date();
        this.isInProgress = false;

        UpdatesState.updateTabContent = true;
        UpdatesState.updateTabs = true;

        State.research.currentProgress = 0;
        State.research.inProgress = false;
        State.research.currentUpgrade = this;

        completeResearchUpgrade(this.codeName);

        if (this.isRepeatable) {
            this.powerNeeded *= this.priceIncrease;
        }

        if (this.onComplete) {
            if (this.onComplete.unlockResearchUpgrade) {
                this.onComplete.unlockResearchUpgrade.forEach((upgrade) => {
                    unlockResearchUpgrade(upgrade, this.codeName);
                });
            }
        }
    };

    this.start = () => {
        console.log('starting a research upgrade');
        Sounds.researchUpgradeStart.play();
        this.isInProgress = true;
        this.startedOn = new Date();

        UpdatesState.updateTabContent = true;
        UpdatesState.updateTabs = true;

        State.research.currentProgress = 0;
        State.research.inProgress = true;
        State.research.currentUpgrade = this;
    };

    this.buy = (event: MouseEvent) => {
        console.log('buying a research upgrade', event);
        if (!State.research.inProgress) {
            if (spend(this.cost, 'refined')) {
                generateRisingText(event, 'buy');
                hideTooltip();
                this.start();
            }
        }
    };

    this.removeNew = (event: any) => {
        this.isNew = false;
        if (event.target.children[1]) {
            removeEl(event.target.children[1]);
        }
    };
};

export const unlockResearchUpgrade = (targetUpgrade: string, previousUpgrade?: string) => {
    const upgrade = State.researchUpgrades[targetUpgrade];

    let unlock = true;

    // Checks to see if there is a requires object
    if (upgrade.requires) {
        // Loop through the object
        for (let upgradeName in upgrade.requires) {
            // Check if key is equal to a just-completed upgrade
            if (upgradeName === previousUpgrade) {
                // Set to true (owned)
                upgrade.requires[upgradeName] = true;
            }
            // If any values are false, don't unlock
            if (upgrade.requires[upgradeName] === false) {
                unlock = false;
            }
        }
    }

    if (unlock) {
        upgrade.isLocked = false;
        if (InstanceState.selectedTab === 'research') {
            UpdatesState.updateTabContent = true;
        } else {
            InstanceState.tabs[1].hasUpdates = true;
            UpdatesState.updateTabs = true;
        }
    }
};

