export const instantiateResearchUpgrades = (): void => {
    const state = JSON.parse(localStorage.getItem('state'));

    const builtUpgrades = {};

    if (state) {
        if (!isObjEmpty(state.researchUpgrades)) {
            for (let key in state.researchUpgrades) {
                builtUpgrades[key] = new ResearchUpgrade(state.researchUpgrades[key]);
            }
        }
    } else {
        researchUpgrades.forEach((upgrade) => {
            builtUpgrades[upgrade.name] = new ResearchUpgrade(upgrade);
        });
    }

    State.researchUpgrades = builtUpgrades;
};

